import React, { useState, useEffect } from 'react';
import loginService from './services/login'
import blogsService from './services/blogs'
import LoginForm from './components/LoginForm'
import Blog from './components/Blog';

const App = () => {
  const [username, setUsername] = useState('')
  const [password, setPassword] = useState('')
  const [blogs, setBlogs] = useState([])
  const [errorMessage, setErrorMessage] = useState(null)
  const [user, setUser] = useState(null)

  useEffect(() => {
    blogsService
      .getAll()
      .then(initialNotes => setBlogs(initialNotes))
  }, [])

  useEffect(() => {
    const loggedUserJSON = window.localStorage.getItem('loggedBlogappUser')
    if (loggedUserJSON) {
      const user = JSON.parse(loggedUserJSON)
      setUser(user)
    }
  }, [])

  const handleLogin = async (event) => {
    event.preventDefault()
    try {
      const user = await loginService.login({ username, password })

      window.localStorage.setItem(
        'loggedBlogappUser', JSON.stringify(user)
      )

      setUser(user)
      setUsername('')
      setPassword('')
    } catch (exception) {
      setErrorMessage('wrong credentials')
      setTimeout(() => {
        setErrorMessage(null)
      }, 5000)
    }
  }

  const handleLogout = () => {
    setUser(null);
    window.localStorage.removeItem('loggedNoteappUser');
  }

  const handleSetUsername = event => setUsername(event.target.value)

  const handleSetPassword = event => setPassword(event.target.value)

  const renderLoginForm = () => {
    return (
      <LoginForm
        username={username}
        password={password}
        setUsername={handleSetUsername}
        setPassword={handleSetPassword}
        handleLogin={handleLogin}
      />
    )
  }

  const renderBlogs = () => blogs.map((blog, index) => <Blog key={index} blog={blog}/>)

  return (
    <div className="App">
      <h1 className="App-header">Blogs</h1>
      <div style={{ color: "red" }}>{errorMessage}</div>
      {!user
        ? renderLoginForm()
        :
        <div>
          <div style={{ display: 'flex', flexDirection: 'row', alignItems: 'center' }}>
            <p>{`${user.name} logged in`}</p>
            <button onClick={handleLogout}>logout</button>
          </div>
          {renderBlogs()}
        </div>
      }
    </div>
  );
}

export default App;
